@import url(https://fonts.googleapis.com/css?family=PT+Sans|PT+Sans+Narrow);
.f-center{display:flex;justify-content:center;align-items:center}.bg-red{background-color:red}body{font-family:"PT Sans",sans-serif;font-size:1.6rem;line-height:1.6;background-color:#cacaca}html{font-size:62.5%}:focus{outline:green solid 2px;z-index:1}.page{min-height:calc(100vh - 120px)}.App{display:grid;min-height:100vh;grid-template-rows:60px 1fr 60px;grid-template-columns:250px 1fr;grid-template-areas:"header header" "sidebar content" "footer footer"}.App .Header{grid-area:header}.App .Footer{grid-area:footer}.App .Sidebar{grid-area:sidebar}.App .content{grid-area:content}.NotFound{background-color:#3e65a0}.Header{background-color:gray}.Footer{background-color:#acacac}.Sidebar{background-color:#5e5e5e;display:flex;flex-direction:column;align-items:center}.Sidebar .links{display:flex;flex-direction:column}

